// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()
scene.background = new THREE.Color( 0x000000 );


/**
 * Loaders
 */
// Texture loader
const textureLoader = new THREE.TextureLoader()

// Draco loader
const dracoLoader = new DRACOLoader()
dracoLoader.setDecoderPath('draco/')

// GLTF loader
const gltfLoader = new GLTFLoader()
gltfLoader.setDRACOLoader(dracoLoader)

// Bake Texture
//const bakedTexture = textureLoader.load("BakedFish.002.png")

// Material
const bakedMaterial = new THREE.MeshBasicMaterial({color: 0xff00000})



let mixer = null
// Save your model reference in a variable
let fish = new THREE.Object3D();
// Model
gltfLoader.load(
    "Fish.gltf",
    (gltf) =>
    {   
       
        mixer = new THREE.AnimationMixer(gltf.scene)
        const action = mixer.clipAction(gltf.animations[0])

        fish = gltf.scene
        fish.name = "fish";
        action.play()
        scene.add(fish)
        fish.scale.normalize()
        fish.position.normalize()
        fish.scale.set(0.08, 0.08, 0.08)

        console.log("loaded")
        console.log(fish.position)
        
    }
    )

    fish = scene.getObjectByName(fish.name)



    

/**
 * Raycaster
 */
 const raycaster = new THREE.Raycaster()  
 

// Light
const light = new THREE.HemisphereLight( 0xfffff, 0x00000, 1 );
scene.add( light );

