/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(45, sizes.width / sizes.height, 0.1, 100)
camera.position.z = 1
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Cursor
 */
 const cursor = {}
 cursor.x = 0
 cursor.y = 0


 window.addEventListener('click', (event) =>
{
    cursor.x = event.clientX / sizes.width * 2 - 1
    cursor.y = - (event.clientY / sizes.height) * 2 + 1

    console.log(cursor)
})

// Cursor Look

const cursor2 = {}
cursor2.x = 0
cursor2.y = 0


window.addEventListener('mousemove', (event) =>
{
    cursor2.x = event.clientX / sizes.width * 2 - 1
    cursor2.y = - (event.clientY / sizes.height) * 2 + 1

    //console.log(cursor2)
})

//Scroll
window.addEventListener("scroll", () => {
    scrollY = window.scrollY
})





/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0



const tick = () =>
{
    
    raycaster.setFromCamera(cursor2, camera)
   

    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime

    // Animation
    if( mixer !== null) {
        mixer.update(deltaTime)
      
        
    }

  
    gsap.to(fish.position, { duration: 10, delay: 0, x: cursor2.x , y: (cursor2.y) , z: 0})
   

   //fish.lookAt(cursor2.x, cursor2.y, 0)

   //fish.position.y = Math.sin(elapsedTime * 0.03) * 0.5
   //fish.position.x = Math.sin(elapsedTime * 0.03) * 0.5



   //sphere.position.y += (parallxY - sphere.position.y) * 4 * deltaTime
   //sphere.position.x += (parallxX - sphere.position.x) * 4 * deltaTime


   //sphere.position.set(cursor2.x, cursor2.y)
    



    // Update controls
    controls.update()

    // Render
    renderer.render(scene, camera)
   

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()